.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f0f2f5; /* Light gray background */
    font-family: 'Arial', sans-serif;
}

.login-card {
    background-color: #ffffff;
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 100%;
    max-width: 400px;
}

.login-title {
    color: #333;
    margin-bottom: 10px;
    font-size: 2.2em;
    font-weight: bold;
}

.login-subtitle {
    color: #666;
    margin-bottom: 30px;
    font-size: 1.1em;
}

.login-form .form-group {
    margin-bottom: 20px;
    text-align: left;
}

.login-form label {
    display: block;
    margin-bottom: 8px;
    color: #555;
    font-weight: bold;
}

.login-form .form-control {
    width: 100%;
    padding: 12px 15px;
    border: 1px solid #ddd;
    border-radius: 5px;
    font-size: 1em;
    box-sizing: border-box; /* Include padding in width */
}

.login-form .form-control:focus {
    border-color: #007bff; /* Bootstrap primary blue */
    outline: none;
    box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0.25);
}

.error-message {
    color: #dc3545; /* Bootstrap danger red */
    margin-top: -10px;
    margin-bottom: 15px;
    font-size: 0.9em;
}

.login-button {
    width: 100%;
    padding: 12px;
    background-color: #007bff; /* Bootstrap primary blue */
    color: white;
    border: none;
    border-radius: 5px;
    font-size: 1.1em;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

.login-button:hover {
    background-color: #0056b3; /* Darker blue on hover */
}

.login-footer {
    margin-top: 30px;
    font-size: 0.9em;
    color: #777;
}

.login-footer a {
    color: #007bff;
    text-decoration: none;
}

.login-footer a:hover {
    text-decoration: underline;
}
